import { writable } from 'svelte/store';
import Helpers from './groups.helpers';
import { groups } from '../data';
const { subscribe, update } = writable(groups);
// const { subscribe, update } = writable([] as GroupModel[]);
// ACTIONS
const addGroup = (label) => update(groups => Helpers.addGroup(groups, label));
// TODO: delete group - how to notify items map, all child items should be deleted, remining relations wiped?
// perhaps need to create no group section
const deleteGroup = (id) => update(groups => Helpers.deleteGroup(groups, id));
// EXPORTS
const GroupsStore = { subscribe, addGroup, deleteGroup };
export { GroupsStore };
