<script lang="ts">
import { createEventDispatcher } from 'svelte';

export let placeholder = '';

const dispatch = createEventDispatcher();
let input = '';

const onClick = (): void => {
  if (input === '' || input === ' ') {
    return;
  }

  dispatch('add', input);
  input = '';
};
</script>

<div class="Input">
  <input class="Input__input" type="text" bind:value={input} {placeholder} />
  <button on:click={onClick}>Add</button>
</div>

<style lang="scss">
.Input {
  display: flex;

  &__input {
    margin-right: 1rem;
  }
}
</style>
