import { derived } from 'svelte/store';
import { GroupsStore } from './groups';
import { ItemsMapStore, RelationsDerived, SelectedItemIdDerived, } from './itemsMap';
import Helpers from './index.helpers';
const createStore = () => {
    const ItemsByGroup = derived([GroupsStore, ItemsMapStore], ([groups, itemMap]) => Helpers.sortItemsByGroup(groups, itemMap));
    // TODO: possible memory leak since unsubscribe not used
    GroupsStore.subscribe(groups => ItemsMapStore.removeGrouplessItems(groups.map(({ id }) => id)));
    return {
        Groups: GroupsStore,
        ItemMap: ItemsMapStore,
        Relations: RelationsDerived,
        SelectedItemId: SelectedItemIdDerived,
        ItemsByGroup,
    };
};
export const { Groups, ItemMap, Relations, ItemsByGroup, SelectedItemId, } = createStore();
